.web {
  display: flex;
  flex: 1;
  align-items: center;
}

.web-option {
  font-size: 15px;
  margin-right: 16px;
  font-weight: 600;
}

.web-option a {
  text-decoration: none;
  color: black;
  display: flex;
  align-items: center;
}

.option-icon {
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.web-option:last-child {
  margin-right: 0px;
}
